import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams} from 'ionic-angular';
// importamos para que aparesca el cargar
import { LoadingController } from 'ionic-angular';
// tambien importe para el alerta si esta vacio el campo de tiempo
import { AlertController } from 'ionic-angular';

import { CaidaLibreTabsPage } from '../caida-libre-tabs/caida-libre-tabs'; 

@IonicPage()
@Component({
  selector: 'page-caida-libre',
  templateUrl: 'caida-libre.html',
})
export class CaidaLibrePage {

  // aca importamos el tamb para movernos
  rootPage: any = CaidaLibreTabsPage;
  // variables del html
  VelocidadInicial;
  tiempo;

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public alertCtrl: AlertController) {
  }

  CalculoVFinal(){
    var velocidadFinal: number;
    var velInicial    = parseFloat(this.VelocidadInicial); // los parseamos para poder hacer poeraciones
    var tiemp         = parseFloat(this.tiempo);
    var gravedad :      number  = 9.81;
    // para las restrincciones
    var paso=false;
      
      if(this.tiempo == null ){
              const alertaerrortiempo = this.alertCtrl.create({
                
                title: 'ERROR!',
                subTitle: 'Obligatorio llenar el campo TIEMPO.',
                buttons: ['Aceptar'],
                cssClass: 'estiloBotonesEmergentes'
              });
              alertaerrortiempo.present();
            // limpio los inputs y vuelvo a null velocidad inicia por defecto
            this.VelocidadInicial = '';
            this.tiempo = '';
            this.VelocidadInicial = null;
            this.tiempo = null;
            paso = false;
      }else if(tiemp < 0){
              // si el tiempo ingresado es negativo
              const alertaerrortiempo = this.alertCtrl.create({
                title: 'ERROR!',
                subTitle: 'El campo tiempo debe contener valor positivo.',
                buttons: ['Aceptar']
              });
              alertaerrortiempo.present();
              // limpio los inputs y vuelvo a null velocidad inicia por defecto
              this.VelocidadInicial = '';
              this.tiempo = '';
              this.VelocidadInicial = null;
              this.tiempo = null;
              paso = false;
            }else {
              paso=true;
            }
// restrinccion para velocidad inicial
    if (this.VelocidadInicial == null) {
      velInicial = 0;
    } else if (velInicial < 0) {
      // si el tiempo ingresado es negativo
      const alertaerrortiempo = this.alertCtrl.create({
        title: 'ERROR!',
        subTitle: 'La velocidad inicial debe ser positiva.',
        buttons: ['Aceptar']
      });
      alertaerrortiempo.present();
      // limpio los inputs y vuelvo a null velocidad inicia por defecto
      this.VelocidadInicial = '';
      this.tiempo = '';
      this.VelocidadInicial = null;
      this.tiempo = null;
      paso = false;
    }

  // ------------------------------ calculo  ---------------------------
      if(paso == true){
          // calculo 
          velocidadFinal = (velInicial + (gravedad * tiemp));
          var resultado = velocidadFinal.toFixed(2); // aca usamos 3 decimales

          const loader = this.loadingCtrl.create({
            content: "Porcesando Calculo...",
            duration: 100,
          });
          loader.present()
            .then(() => {
              const calculo = this.alertCtrl.create({
                title: 'RESULTADO',
                subTitle: 'La Velocidad Final es: ' + resultado + ' m/s',
                buttons: ['Aceptar'],
              });
              calculo.present();
            });
          // limpio los inputs y vuelvo a null velocidad inicia por defecto
          this.VelocidadInicial = '';
          this.tiempo = '';
          this.VelocidadInicial = null;
          this.tiempo = null;
          paso = false;
      }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CaidaLibrePage');
  }

}



