import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

// aca se declaran todas las paginas 
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';

// importamos todos nuestros componentes de caida libre
import { CaidaLibrePage } from '../pages/caida-libre/caida-libre';
import { CaidaLibreAlturaPage } from '../pages/caida-libre-altura/caida-libre-altura';
import { CaidaLibreTabsPage } from '../pages/caida-libre-tabs/caida-libre-tabs';
import { CaidaLibreTiempoPage } from '../pages/caida-libre-tiempo/caida-libre-tiempo';

// componentes de nergia cinetica
import { EnergiaCineticaPage } from '../pages/energia-cinetica/energia-cinetica';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    CaidaLibrePage,    // aca declaramos nuestro model de caida libre
    CaidaLibreAlturaPage,
    CaidaLibreTabsPage,
    CaidaLibreTiempoPage,
    EnergiaCineticaPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    CaidaLibrePage,  // aca declaramos nuestro model de caida libre
    CaidaLibreAlturaPage,
    CaidaLibreTabsPage,
    CaidaLibreTiempoPage,
    EnergiaCineticaPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



